import { loadFragment, loadLocalSound } from '../components';
import { feedbackRight, feedbackWrong, feedbackRightEn, feedbackWrongEn } from '../assets/images';
import { LS00000300, LS00000301 } from '../assets/sounds';
import { SOL } from '.';
import { Howler } from 'howler';
import Snap from '../snap';
export default function feedback({
    canvas,
    el,
    gap = [0, 0],
    bool,
    onRemove,
    rotation = 0,
    scale = 1,
    duration = 300,
    addAction = true,
    howlerStop = true
}) {
    const icons = SOL.subjectCode === 'E1' ? [feedbackWrongEn, feedbackRightEn] : [feedbackWrong, feedbackRight];
    const svg = canvas || SOL.SVG;
    const callbackList = [];
    // 추가 터치 방지
    const bgRect = svg.rect(0, 0, 800, 500).addClass('f00').attr({
        opacity: 0
    });
    callbackList.push(bgRect.remove.bind(bgRect));
    // 피드백 엘리먼트
    if (el) {
        const iconSize = [52.348, 58.61].map(el => el * scale); // 이미지 사이즈
        let posX, posY;
        if (Array.isArray(el)) {
            //값
            posX = el[0] - iconSize[0] / 2;
            posY = el[1] - iconSize[1];
        } else {
            //element
            const elTBox = el.getTBox();
            posX = elTBox.x2 - iconSize[0] / 2 + gap[0];
            posY = elTBox.y - iconSize[1] + gap[1];
        }
        const icon = svg.g().transform('t' + [posX, posY] + 'r' + rotation + 's' + scale);

        Snap(
            loadFragment({
                src: icons[Number(bool)],
                key: 'feedback' + (Number(bool) + 1),
                type: 'svg'
            }).node
        ).appendTo(icon);
        callbackList.push(icon.remove.bind(icon));
        if (howlerStop) Howler.stop();
        loadLocalSound([LS00000301, LS00000300][Number(bool)]).play();
    }
    if (onRemove) callbackList.push(onRemove);
    if (addAction) callbackList.push(SOL.controller.addUserAction.bind(SOL.controller, Number(bool)));
    setTimeout(callbackList.forEach.bind(callbackList), duration, func => func());
    return {
        addUserAction: SOL.controller.addUserAction.bind(SOL.controller, Number(bool))
    };
}
