import { text as basicText, tts } from './';

const ttsAndText = ({
    canvas,
    text,
    ttsText,
    ttsX = 30,
    ttsY = 10,
    x = 70,
    y = 30,
    className,
    highlightClassNames
}) => {
    const g = canvas.g();

    tts({
        canvas: g,
        x: ttsX,
        y: ttsY,
        text: ttsText || text
    });
    basicText({
        canvas: g,
        text,
        x,
        y,
        className,
        highlightClassNames
    });
    return g;
};
export default ttsAndText;
